import { useEffect, useState } from "react";
import Header from "./Components/Header";
import classes from "./App.module.css";
import Card from "./Card";
import image1 from "./sarah4.jpg";
import image2 from "./sarah2.PNG";
import image3 from "./sarah3.PNG";
import image4 from "./sarah5.jpg";
import image5 from "./sarah6.jpg";
import image6 from "./sarah8.jpg";
import image7 from "./sarah10.jpg";
import image8 from "./dog7.jpg";
// import image from "./bubble.jpeg";
// import image from "./bubble.jpeg";

function App() {
  const [remainingDays, setRemainingDays] = useState("00");
  const [hoursUntill, setHoursUntil] = useState("00");
  const [minutesUntil, setMinutesUntil] = useState("00");
  const [secondsUntil, setSecondsUntil] = useState("00");
  const [controller, setController] = useState(null);
  const [showPix, setShowPix] = useState(false);
  // const [isbtn, setIsBtn] = useState(false);

  useEffect(() => {
    const timer = setInterval(() => {
      const days = new Date().getTime();
      const birthday = new Date("May 2, 2022 18:45:30").getTime();

      const daysUntill = (birthday - days) / (1000 * 60 * 60 * 24);
      if (birthday - days < 0) {
        setController(true);
        return;
      }
      setRemainingDays(Math.floor(daysUntill.toFixed(0)));
      setHoursUntil(((daysUntill * 24) % 24).toFixed(0));
      setMinutesUntil(Math.floor((daysUntill * (24 * 60)) % 60));
      setSecondsUntil(Math.floor((daysUntill * (24 * 60 * 60)) % 60));
    }, 1000);
    return () => clearInterval(timer);
  }, []);

  // const clickHandler = () => {
  //   setIsBtn(true);
  // };

  // useEffect(() => {
  //   setInterval(() => {
  //     setIsBtn(false);
  //   }, 8000);
  // }, []);

  const pictureHandler = () => {
    setShowPix(true);
  };
  return (
    <Card load={controller}>
      <Header />
      {/* {isbtn && (
        <h1 className={classes.hbtn}>
          Hey Hope you are FULLY RECOVERED SOON!!! Daddy does not like his
          little girl sick
        </h1>
      )} */}

      <div className={classes.container}>
        {!controller && (
          <h2 className={classes.h2}>
            Days To Your BirthDay:
            <span>{`  ${remainingDays} Days: ${hoursUntill}: ${minutesUntil}: ${secondsUntil}`}</span>
          </h2>
        )}
        {controller && (
          <div>
            <h1 className={classes.h1}>HAPPY 25th BIRTHDAY SARAH DUBBEL</h1>
            {!showPix && (
              <button
                onClick={pictureHandler}
                style={{
                  cursor: "pointer",
                  borderRadius: "1rem",
                  color: "grey",
                }}
              >
                Discover Treasure
              </button>
            )}
            <div className={classes.bubbleimg}>
              <img
                src={image1}
                alt="bubble"
                style={{ borderRadius: "50%", width: "50px" }}
              />
              <img
                src={image2}
                alt="bubble "
                style={{ borderRadius: "50%", width: "50px" }}
              />
              <img
                src={image3}
                alt="bubble"
                style={{ borderRadius: "50%", width: "50px" }}
              />
              <img
                src={image4}
                alt="bubble"
                style={{ borderRadius: "50%", width: "50px" }}
              />
              <img
                src={image5}
                alt="bubble "
                style={{ borderRadius: "50%", width: "50px" }}
              />
              <img
                src={image6}
                alt="bubble"
                style={{ borderRadius: "50%", width: "50px" }}
              />
              <img
                src={image8}
                alt="bubble"
                style={{ borderRadius: "50%", width: "50px" }}
              />
            </div>
            {showPix && (
              <img src={image7} style={{ width: "230px" }} alt="sarah" />
            )}
          </div>
        )}
        {/* <button className={classes.btn} onClick={clickHandler}>
          Hey Sick Beauty!!! Click Me
        </button> */}
      </div>
    </Card>
  );
}

export default App;
